import { Router } from 'preact-router'
import { useState, useEffect } from 'preact/hooks'
import AsyncRoute from 'preact-async-route'
import CDHome from './routes/index'
import CDHomeVersion from './routes/homeversion'
import './style'
import 'react-phone-input-2/lib/style.css'
import UserContext from './context/userContext'
const App = () => {
  const [userGeoLocation, setUserGeoLocation] = useState({})
  if (typeof window !== 'undefined') {
    const location = window.location.pathname
    const origin = window.location.origin
    if ((location === "/" && origin === "https://lp.canadadistrict.com") || (location === "/" && origin === "https://wbsttest.github.io"))
      window.location.href = "/lp/en/global/immigration/";
  }
  const originData = async () => {
    const res = await fetch("https://ipapi.co/json");
    const jsonData = await res.json();
    setUserGeoLocation(jsonData || {});
  };
  useEffect(() => {
    originData()
  }, [])
  return (
    <div id='app'>
      <main>
        <UserContext.Provider value={{ userGeoLocation, setUserGeoLocation }}>
          <Router
          onChange={() =>
            typeof window !== 'undefined' && window.scrollTo(0, 0)
          }
        >
          <CDHome path="/lp/en/global/immigration/" />
          <CDHomeVersion path="/lp/en/global/immigration-2/" />
          <AsyncRoute
            path="/thank-you/email_verification/"
            getComponent={() =>
              import('./components/thank-you').then(
                (module) => module.default,
              )
            }
          />
          <AsyncRoute
            path="/thank-you/unsubscribe/"
            getComponent={() =>
              import('./components/thank-you').then(
                (module) => module.default,
              )
            }
          />
          <AsyncRoute
              path="/thank-you/"
              getComponent={() =>
                import("./components/thank-you").then(
                  (module) => module.default
                )
              }
            />
            </Router>
        </UserContext.Provider>
      </main>
    </div>
  );
};
export default App;